<template>
  <form class="col-md-8 como-funciona-form card">
    <div class="form-field mx-auto" v-for="pergunta in perguntas" v-show="step == pergunta.step">
      <p-component classe="fs-title" :texto="pergunta.titulo"></p-component>
      <p-component classe="fs-subtitle" :texto="pergunta.subtitulo"></p-component>
      <div class="row mx-auto questoes">
        <radio-component v-for="(questao, key) in pergunta.questoes" classediv="perguntas mx-auto" :key="pergunta.index" :id="questao.nome"
        :labelradio="questao.nome" type="radio" :name="pergunta.name" :label="questao.label" :valor="questao.nome" :peso="questao.peso" v-model="pergunta.vmodel" @finished="clicked">
        </radio-component>
      </div>
      <!-- <p-component :texto="pergunta.vmodel"></p-component> -->
      <div class="col-md-12">
        <button-component @click.native.prevent="prev('')" v-if="pergunta.step != 0" tipo="button" classe="btn btn-default-2" rotulo="Anterior"></button-component>
        <button-component @click.native.prevent="next('active')" tipo="button" classe="btn btn-outline" rotulo="Próximo"></button-component>
      </div>
    </div>
  </form>
</template>

<script>
  import SelectComponent from '../shared/SelectComponent.vue'
  import PComponent from '../shared/PComponent.vue'
  import ButtonComponent from '../shared/ButtonComponent.vue'
  import InputComponent from '../shared/InputComponent.vue'
  import RadioComponent from '../shared/RadioComponent.vue'
  import SimulacaoComponent from '../comoFunciona/SimulacaoComponent.vue'
  export default {
    name: 'como-funciona-form-component',
    components: {
      SelectComponent, PComponent, ButtonComponent, InputComponent, RadioComponent, SimulacaoComponent
    },
    props: { peso: Number },
    data() {
      return {
        step: 0,
        labelAtual: '',
        final: 0,
        scoreFinal: 0,
        perguntas: [
          {
            step: 0,
            titulo: "Faixa etária",
            subtitulo: "Qual é a sua faixa etária?",
            name: "idade",
            questoes: [
              { nome: "jovem", label: "18 a 25 anos (Jovem)", peso: 250}, { nome: "meiaidade", label: "26 a 34 anos (Meia idade)", peso: 100},
              { nome: "adulto", label: "35 a 60 anos (Adulto)", peso: 200}, { nome: "terceiraidade", label: "Acima de 60 anos (Idoso)", peso: 250}
            ]
          },
          {
            step: 1,
            titulo: "Renda familiar",
            subtitulo: "Qual é a sua renda familiar?",
            name: "renda",
            questoes: [
              { nome: "classeE", label: "Até 1 salários mínimos", peso: 50}, { nome: "classeD", label: "De 1 a 3 salários mínimos", peso: 100},
              { nome: "classeC", label: "De 3 a 10 salários mínimos", peso: 175}, { nome: "classeB", label: "De 10 a 20 salários mínimos", peso: 300},
              { nome: "classeA", label: "Acima de 20 salários mínimos", peso: 350}
            ]
          },
          {
            step: 2,
            titulo: "Estado civil",
            subtitulo: "Qual é o seu estado civil?",
            name: "estadocivil",
            questoes: [
              { nome: "solteiro", label: "Solteiro(a)", peso: 100}, { nome: "casado", label: "Casado(a)", peso: 150},
              { nome: "divorciado", label: "Divorciado(a)", peso: 50},{ nome: "viuvo", label: "Viúvo(a)", peso: 200}
            ]
          },
          {
            step: 3,
            titulo: "Escolaridade",
            subtitulo: "Qual é a sua escolaridade?",
            name: "escolaridade",
            questoes: [
              { nome: "fundamental", label: "Ensino fundamental", peso: 200},
              { nome: "medio", label: "Ensino médio", peso: 190}, { nome: "superior", label: "Ensino superior", peso: 100},
              { nome: "posgraduacao", label: "Pós graduação", peso: 150}, { nome: "mestrado", label: "Mestrado", peso: 150}
            ]
          },
          {
            step: 4,
            titulo: "Cartão de crédito",
            subtitulo: "Você possui cartão de crédito?",
            name: "cartaocredito",
            questoes: [
              { nome: "sim", label: "Sim", peso: 150}, { nome: "nao", label: "Não", peso: 50}
            ]
          },
          {
            step: 5,
            titulo: "Financiamento",
            subtitulo: "Você possui algum financiamento em seu nome?",
            name: "financiamento",
            questoes: [
              { nome: "financiado", label: "Sim", peso: -50}, { nome: "naofinanciado", label: "Não", peso: 50}
            ]
          },
          {
            step: 6,
            titulo: "Negativação",
            subtitulo: "Você já foi negativado?",
            name: "negativacao",
            questoes: [
              { nome: "janegativado", label: "Sim", peso: -150}, { nome: "nuncanegativado", label: "Não", peso: 0}
            ]
          },
          {
            step: 7,
            titulo: "Negativado",
            subtitulo: "Você está negativado atualmente?",
            name: "negativado",
            questoes: [
              { nome: "estanegativado", label: "Sim", peso: -150}, { nome: "naonegativado", label: "Não", peso: 0}
            ]
          },
          {
            step: 8,
            titulo: "Tempo negativado",
            subtitulo: "Há quanto tempo está ou já esteve negativado?",
            name: "temponegativado",
            questoes: [
              { nome: "30dias", label: "Menos de 30 dias", peso: -10 }, { nome: "60dias", label: "Menos de 60 dias", peso: -50},
              { nome: "90dias", label: "Menos de 90 dias", peso: -100}, { nome: "180dias", label: "Menos de 180 dias", peso: -200},
              { nome: "acima180dias", label: "Mais de 180 dias", peso: -300}
            ]
          }
        ]
      }
    },
    methods: {
      prev(classe) {
        this.labelAtual = undefined
        this.$emit('clickPrev', classe, this.step)
        this.step--
      },
      next(classe) {
        if(this.final == 0)
        {
          if(this.labelAtual != undefined)
          {
            this.step++
            this.labelAtual = undefined
            this.$emit('clickNext', classe, this.step)
          }
        }else
        {
          this.$emit('salvaScore')
        }
      },
      clicked(peso, valor, label)
      {
        if(label == 'naonegativado' || this.step == 8 || label == 'nuncanegativado')
        {
          this.final = 1
        }else {
          this.final = 0
        }
        this.labelAtual = valor
        this.$emit('setPeso', peso)
      }
    }

  }
</script>

<style lang="scss">
  .como-funciona-form{
    text-align: center;
    position: relative;
    margin-bottom: 0;
    .form-field{
      padding: 3vh 2vw;
      padding-bottom: 1vh;
      height: 100%;
      width: 50vw;
      .questoes{
        height: 18vh;
        margin-top: 7vh;
        margin-bottom: 3vh;
      }
    }
  }
</style>
