<template lang="html">
  <fb-signin-button class="fb-signin-button "
    :params="fbSignInParams"
    @success="onSignInSuccess"
    @error="onSignInError">
  </fb-signin-button>
</template>

<script>

export default {
  name: 'facebook-login',
  data () {
    return {
      fbSignInParams: {
        scope: 'email,user_likes,user_link',
        return_scopes: true
      }
    }
  },
  methods: {
    onSignInSuccess (response) {
      console.log(response);
      console.log(response.email);
      FB.api('/me?fields=email,name,id', function(usuario) {
        console.log(usuario);
        console.log(`Good to see you, ${usuario.name}.`)
      })
    },
    onSignInError (error) {
      console.log('OH NOES', error)
    }
  }
}
</script>

